<template>
  <div class="self-files-container flex">
    <div class="left-menu h-full">
      <Dropdown :dropMenuList="getAddMenuList" @menuEvent="handleDropdown">
        <el-button type="primary" class="drop-add-folder w-full">新建</el-button>
      </Dropdown>
      <SingleMenu
        :defaultValue="activeMenu.value"
        :defaultSpaceId="activeMenu.spaceId"
        :menus="menuList"
        class="mt-5"
        @handleChange="handleMenuChange"
      />
      <SingleFileNum class="single-file-num"/>
      <SingleBottom class="single-bottom"/>
    </div>
    <div class="right-layout h-full flex-1" v-loading="contentLoading">
      <OnlyFileContent
        v-if="activeMenu.value == MenuValueEnum.MY_COMMON_USE.valueOf() || activeMenu.value == MenuValueEnum.RECENT_OPEN.valueOf()"/>
      <TeamContent v-if="activeMenu.value == MenuValueEnum.MY_FILE.valueOf()"/>
      <RecentChangesContent v-if="activeMenu.value == MenuValueEnum.RECENT_CHANGE.valueOf()"/>
      <RecylebinContent v-if="activeMenu.value == MenuValueEnum.RECYLE_BIN.valueOf()"/>
    </div>
  </div>
  <SimpleDialog v-model:visible="folderData.visible" :title="folderData.title" :footer="false">
    <FolderForm @formResponse="handleFolderRes" key="add-form"/>
  </SimpleDialog>
  <SimpleDialog
    v-model:visible="websiteData.visible"
    width="600px"
    :title="websiteData.title"
    :footer="false"
  >
    <WebsiteForm @formResponse="handleWebsiteRes" key="add-website"/>
  </SimpleDialog>
</template>

<script setup lang="ts">
import Dropdown from '@/components/Dropdown/Dropdown.vue'
import SingleMenu from '@/components/SingleMenu/SingleMenu.vue'
import SingleFileNum from '@/components/SingleFileNum/SingleFileNum.vue'
import SingleBottom from '@/components/SingleBottom/SingleBottom.vue'
import SimpleDialog from '@/components/SimpleDialog/SimpleDialog.vue'
import TeamContent from "./TeamContent.vue";
import OnlyFileContent from "./OnlyFileContent.vue"
import RecentChangesContent from './RecentChangesContent.vue'
import RecylebinContent from './RecylebinContent.vue'
import FolderForm from '@/components/ContentElement/folder/FolderForm.vue'
import WebsiteForm from '@/components/ContentElement/website/WebsiteForm.vue'
import {useFolderForm} from '@/components/ContentElement/folder/useFolderForm'
import {useWebsiteForm} from "@/components/ContentElement/website/useWebsiteForm";
import {useConst} from '@/components/ContentElement/const'
import {DropdownEnum} from '@/enums/dropdownEnum'
import {useFiles} from '@/hooks/components/useFiles'
import {computed, ref, watch} from 'vue'
import {useOnEmitFn} from '@/utils/useEmitter'
import {MENU_MY_COMMON_USE, MENU_MY_FILE, MENU_RECENT_CHANGE, MENU_RECENT_OPEN, MENU_RECYLE_BIN} from "@/consts/menu";
import {useTeamStore} from "@/store/modules/team";
import {MenuValueEnum, personalMenuValues} from "@/enums/menuValueEnum";
import {fetchPersonalSpace} from "@/api/fetchs/team";
import {MenuProps} from "@/components/SingleMenu/useSingleMenu";

const menuList = ref<MenuProps[]>([])
const teamStore = useTeamStore()

const {handleFolderMenuChange} = useFiles()

const {getAddMenuList} = useConst()

const contentLoading = ref<boolean>(false)

const activeMenu = computed(() => teamStore.getActiveMenu || ref<MenuProps>({value: MenuValueEnum.MY_COMMON_USE}))

const {folderData, handleFolderVisible, handleFolderRes} = useFolderForm()
const {websiteData, handleWebsiteVisible, handleWebsiteRes} = useWebsiteForm()

function handleDropdown(dropItem) {
  switch (dropItem.key) {
    case DropdownEnum.ADD_FOLDER:
      handleFolderVisible(dropItem.text)
      break
    case DropdownEnum.ADD_WEBSITE:
      handleWebsiteVisible(dropItem.text)
      break
  }
}

function handleMenuChange(menu) {
  handleFolderMenuChange(menu)
}

function handleChangeLoadingStatus(show: boolean) {
  contentLoading.value = show
}

useOnEmitFn('handleChangeLoadingStatus', handleChangeLoadingStatus)

watch(
  () => teamStore.getActiveTeamId,
  () => {
    getMenuList()
  }, {immediate: true})

/* ================================== fetchs ================================== */
async function getMenuList() {
  let teamId = teamStore.getActiveTeamId
  if (!teamId) {
    return
  }

  let teamType = teamStore.getActiveTeamType
  if (!teamType || 'PERSONAL' !== teamType) {
    return
  }

  const {data} = await fetchPersonalSpace(teamStore.getActiveTeamId)

  MENU_MY_FILE.spaceId = data.spaceId
  MENU_RECENT_CHANGE.spaceId = data.spaceId
  MENU_RECYLE_BIN.spaceId = data.spaceId

  let menus = [
    MENU_MY_COMMON_USE,
    MENU_RECENT_OPEN,
    MENU_MY_FILE,
    MENU_RECENT_CHANGE,
    MENU_RECYLE_BIN
  ]

  menuList.value = menus

  if (!teamStore.getActiveMenu) {
    teamStore.setActiveMenu(menus[0])
    return
  }

  let activeMenuValue = personalMenuValues.find(v => v == teamStore.getActiveMenu.value)
  if (activeMenuValue == undefined || activeMenuValue == null) {
    teamStore.setActiveMenu(menus[0])
  }
}

</script>

<style lang="scss" scoped>
.self-files-container {
  height: calc(100vh - 50px);

  .left-menu {
    position: relative;
    padding: 20px 20px 0;
    width: 206px;
    border-right: 1px solid #ddd;

    .drop-add-folder {
      width: 165px;
      margin-bottom: 8px;
    }

    .single-file-num {
      position: absolute;
      bottom: 65px;
      width: 80%;
    }

    .single-bottom {
      position: absolute;
      bottom: 15px;
    }
  }

  .right-layout {
    background-color: #f4f4f4;
    width: calc(100% - 200px);
    padding: 15px 0;
  }
}
</style>
