<template>
  <div class="list-header-section flex justify-between">
    <div class="w-full flex justify-between" v-if="fileCheckList.length === 0">
      <Breadcrumb v-if="showBreadcrumbValues.includes(teamStore.getActiveMenu.value)"
                  @handleBreadcrumbClick="handleBreadcrumbClick"
      />
      <span class="root-title" v-else>{{ teamStore.getActiveMenu.text }}</span>
      <Actions
        @handleClearAll="handleClearAll"
        @handleSearch="handleSearch"
        @handleSort="handleSort"
        :sortType="sortType"
      />
    </div>
    <MultipleSelect
      :multipleList="fileCheckList"
      :defaultChecked="fileCheckList.length === getWebsiteList.length"
      :checkedTotal="fileCheckList.length"
      @checkAll="handleCheckAll"
      @close="handleCloseMultiple"
      v-else
    />
  </div>

  <div class="menu-view-container last-update-continer">
    <div class="menu-scrollbar">
      <div class="last-update" v-for="u in lastUpdateList" :key="u.name">
        <el-divider
          content-position="left"
          v-show="u.folderList.length > 0 || u.websiteList.length > 0"
        >
          {{ u.name }}
        </el-divider>
        <p class="menu-title" v-show="u.folderList.length > 0">文件夹</p>
        <el-row :gutter="15">
          <el-col
            v-for="item in u.folderList"
            :key="item.id"
            :xs="8"
            :sm="6"
            :md="4"
            :lg="3"
            :xl="3"
          >
            <Folder :folder-item="item" :can-click="false" :item-menu-list="commonFolderMenuList" @handleFolderClick="handleFolderClick"/>
          </el-col>
        </el-row>
        <p class="menu-title" v-if="u.websiteList.length > 0">文件</p>
        <el-checkbox-group v-model="fileCheckList" @change="handleFileCheckChange">
          <el-row :gutter="15">
            <el-col
              v-for="item in u.websiteList"
              :key="item.id"
              :xs="12"
              :sm="8"
              :md="6"
              :lg="4"
              :xl="4"
            >
              <Website :item-menu-list="commonWebsiteMenuList" :website-item="item" :show-checkbox="true"
                       :checked-state="fileCheckList.length > 0"/>
            </el-col>
          </el-row>
        </el-checkbox-group>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import Folder from '@/components/ContentElement/Folder.vue'
import Website from '@/components/ContentElement/Website.vue'
import Breadcrumb from '@/components/ContentElement/Breadcrumb.vue'
import {useConst} from '@/components/ContentElement/const'
import Actions from '@/components/ContentElement/actions/index.vue'
import MultipleSelect from '@/components/ContentElement/multiple-select/index.vue'
import {computed, onBeforeUnmount, reactive, ref, unref, watch, watchEffect} from "vue";
import {DataTypeEnum,FileItem} from "@/components/ContentElement/types";
import {SortTypeEnum} from "@/api/model/filesModel";
import {getSearchParams} from "@/utils/form";
import {useLocalStorage} from "@/utils/cache";
import {SORT_TYPE_KET} from "@/enums/keyEnum";
import {useTeamStore} from "@/store/modules/team";
import {showBreadcrumbValues} from "@/enums/menuValueEnum";
import {fetchList} from "@/api/fetchs/files";
import {useEmit, useOffEmitFn, useOnEmitFn} from "@/utils/useEmitter";
import {getDateToTimestamp, getTimestamp, isInCurrentMonth, isInCurrentWeek, isInLastWeek} from "@/utils/time";

interface LastUpdateList {
  name: string
  folderList: FileItem[]
  websiteList: FileItem[]
}

const {commonFolderMenuList, commonWebsiteMenuList, getLastUpdateNameMaps} = useConst()
const teamStore = useTeamStore()

const sortType = ref<SortTypeEnum>(useLocalStorage.get(SORT_TYPE_KET) ?? SortTypeEnum.CUSTOM)
const fileCheckList = ref<string[]>([])
const list = ref<FileItem[]>([])
const lastUpdateList = ref<LastUpdateList[]>([])
const listQuery = reactive({
  teamId: teamStore.getActiveTeamId,
  spaceId: teamStore.getActiveMenu.spaceId,
  search: '',
  sortType: SortTypeEnum.LAST_UPDATE_TIME,
  resource: 'history'
})

const activeTeamSpaceId = computed(() => teamStore.getActiveMenu.spaceId)

const getWebsiteList = computed(() =>
  unref(list).filter(v => v.dataType === DataTypeEnum.WEBSITE)
)

function handleBreadcrumbClick(id) {
  let pid = id
  if(pid?.startsWith('root:')) {
    pid = 'root'
  }
  handleUpdateListByPid(pid)
}

function handleSearch(val) {
  listQuery.search = val
  getMySpaceFileList()
}
function handleSort(val) {
  listQuery.sortType = val
  useLocalStorage.set(SORT_TYPE_KET, val)
  getMySpaceFileList()
}
// handleFileCheckChange
function handleFileCheckChange() {
  useOffEmitFn('handleUpdateTreeData')
}
// 全选
function handleCheckAll(val) {
  fileCheckList.value = val ? unref(getWebsiteList).map(v => v.id) : []
}
// 取消批量选择
function handleCloseMultiple() {
  fileCheckList.value = []
}

function handleUpdateListByPid(pid) {
  // @ts-ignore
  listQuery.pid = pid
  getMySpaceFileList()
}

function handleFolderClick() {
  fileCheckList.value = []
}

handleUpdateListByPid('root')

useOnEmitFn('handleUpdateList', getMySpaceFileList)
useOnEmitFn('handleUpdateListByPid', handleUpdateListByPid)

onBeforeUnmount(() => {
  useOffEmitFn('handleUpdateList')
  useOffEmitFn('handleUpdateListByPid')
})

/* ================================== fetchs ================================== */
async function getMySpaceFileList() {
  useEmit('handleChangeLoadingStatus', true)

  try {
    listQuery.spaceId = unref(activeTeamSpaceId)
    const params = getSearchParams(listQuery)
    const {data} = await fetchList(params)
    list.value = data.folders.concat(data.files)

    const daysArray = ['today', 'yesterday', 'week', 'lastWeek', 'month', 'long']
    const dataIds = []
    lastUpdateList.value = daysArray.map(v => {
      const flist = data.folders.filter(d => {
        if(dataIds.includes(d.id)) {
          return false
        }
        const timestamp = getDateToTimestamp(d.updateTime)
        if(v === 'today') {
          return timestamp > getTimestamp(1)
        }else if (v === 'yesterday') {
          return timestamp > getTimestamp(2) && timestamp <= getTimestamp(1)
        } else if (v === 'week') {
          return isInCurrentWeek(d.updateTime)
        } else if (v === 'lastWeek') {
          return isInLastWeek(d.updateTime)
        } else if (v === 'month') {
          return isInCurrentMonth(d.updateTime)
        } else if (v === 'long') {
          return !isInCurrentMonth(d.updateTime)
        }
      })
      flist?.forEach(item => {
        dataIds.push(item.id)
      })
      const wlist = data.files.filter(d => {
        if(dataIds.includes(d.id)) {
          return false
        }
        const timestamp = getDateToTimestamp(d.updateTime)
        if(v === 'today') {
          return timestamp > getTimestamp(1)
        } else if (v === 'yesterday') {
          return timestamp > getTimestamp(2) && timestamp <= getTimestamp(1)
        } else if (v === 'week') {
          return isInCurrentWeek(d.updateTime)
        } else if (v === 'lastWeek') {
          return isInLastWeek(d.updateTime)
        } else if (v === 'month') {
          return isInCurrentMonth(d.updateTime)
        } else if (v === 'long') {
          return !isInCurrentMonth(d.updateTime)
        }
      })
      wlist?.forEach(item => {
        dataIds.push(item.id)
      })
      return {
        name: getLastUpdateNameMaps[v],
        folderList: flist,
        websiteList: wlist
      }
    })
  }finally {
    useEmit('handleChangeLoadingStatus', false)
  }
}

</script>

<style lang="scss" scoped>
@import '@/components/ContentElement/content.scss';
</style>
