<template>
  <div class="single-file-num-container">
    <div class="file-num">
<!--      <span>文件数 {{ fileNum.userFileNum }}/{{ fileNum.totalFileNum }}</span>-->
      <span>文件数 {{ fileNum.userFileNum }}</span>
<!--      <el-link type="primary" :underline="false" @click="handleClick('升级', $event)">升级</el-link>-->
    </div>
    <el-progress
      :percentage="fileNum.userFileNum"
      :color="customColors"
      :show-text="false"
      :stroke-width="10"
    />
<!--    <div class="free-expand">-->
<!--      <el-link type="primary" :underline="false">免费扩容</el-link>-->
<!--    </div>-->
  </div>
</template>

<script lang="ts" setup>
//@ts-ignore
import {ref, unref, watch} from 'vue'
import {fetchFileCount} from '@/api/fetchs/files'
import emitter from "@/utils/useEmitter";
import {useGo} from "@/hooks/web/usePage";
import {bottomMenus} from "@/layout/default/header/components/menu/consts";
import {useTeamStore} from "@/store/modules/team";

const customColors = [
  {color: '#67C23A', percentage: 60},
  {color: '#E6A23C', percentage: 80},
  {color: '#F56C6C', percentage: 100}
]

const teamStore = useTeamStore()

interface FileNum {
  userFileNum?: number
  totalFileNum?: number
}

const fileNum = ref<FileNum>({userFileNum: 0, totalFileNum: 100})

emitter.on('*', (index, data) => {
  if (index === 'handleUpdateList') {
    getFileCount()
  }
})

const go = useGo()

function handleClick(menuName: string, evt: any){
  let menu;

  for (const m of bottomMenus) {
    if (m.name == menuName) {
      menu = m
      break
    }
  }

  if(menu) {
    if(menu.path.indexOf('http') == 0) {
      window.location.href = menu.path
    } else {
      go(menu.path)
    }
  }
}

watch(
  () => teamStore.getActiveTeamId,
  () => {
    getFileCount()
  },{ immediate: true })

/* ================================== fetchs ================================== */

async function getFileCount() {
  let teamId = teamStore.getActiveTeamId
  if(!teamId) {
    return
  }

  const {data} = await fetchFileCount(teamId)

  fileNum.value = {userFileNum: data.fileCount, totalFileNum: data.totalCount}
}

</script>

