<template>
	<div class="single-bottom-container">
		<div class="bottom-style">
			<el-link type="default" :underline="false" @click="handleClick('帮助手册', $event)">帮助手册</el-link>
			<el-link type="default" :underline="false">反馈</el-link>
		</div>
		<div class="bottom-style">
			<el-link type="default" :underline="false" @click="handleClick('服务条款', $event)">服务条款</el-link>
			<el-link type="default" :underline="false" @click="handleClick('关于我们', $event)">关于我们</el-link>
		</div>
	</div>
</template>

<script setup lang="ts">
  import {bottomMenus} from "@/layout/default/header/components/menu/consts"
  import { useGo } from '@/hooks/web/usePage'

  const go = useGo()

  function handleClick(menuName: string, evt: any){
    let menu;

    for (const m of bottomMenus) {
      if (m.name == menuName) {
        menu = m
        break
      }
    }

    if(menu) {
      if(menu.path.indexOf('http') == 0) {
        window.location.href = menu.path
      } else {
        go(menu.path)
      }
    }
  }
</script>

<style lang="scss" scoped>
	.single-bottom-container {
		.bottom-style {
			.el-link {
				font-size: 12px;
				margin-right: 20px;
			}
		}
	}
</style>
