import moment from "moment"

// 毫秒级
export function getTimestamp(day: number): number {
	const timestamp = new Date(
		new Date(new Date().toLocaleDateString()).getTime() - (day - 1) * 24 * 3600 * 1000 - 1
	).getTime()
	return timestamp
}
// 毫秒级
export function getDateToTimestamp(dateStr: string): number {
	const newstr = dateStr.replace(/-/g, '/')
	const date = new Date(newstr)
	const time_str = date.getTime().toString()
	return Number(time_str.substr(0, 10)) * 1000
}

export function timestampToTime(timestamp) {
	const date = new Date(timestamp) //时间戳为10位需*1000，时间戳为13位的话不需乘1000
	const Y = date.getFullYear() + '-'
	const M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-'
	const D = date.getDate() + ' '
	const h = date.getHours() + ':'
	const m = date.getMinutes() + ':'
	const s = date.getSeconds()
	return Y + M + D + h + m + s
}

export function isInCurrentWeek(date) {
  let weekOfDay = parseInt(moment().format('E'));//计算今天是这周第几天
  let monday = moment().subtract(weekOfDay - 1, 'days').format('YYYY-MM-DD') + '00:00:00';//周一日期
  let sunday = moment().subtract(weekOfDay - 7, 'days').format('YYYY-MM-DD') + '23:59:59';//周日日期

  return date >= monday && date <= sunday

}

export function isInLastWeek(date) {
  let weekOfDay = parseInt(moment().format('E'));//计算今天是这周第几天
  let monday = moment().subtract(weekOfDay + 6, 'days').format('YYYY-MM-DD') + '00:00:00';//周一日期
  let sunday = moment().subtract(weekOfDay, 'days').format('YYYY-MM-DD') + '23:59:59';//周日日期

  return date >= monday && date <= sunday

}

export function isInCurrentMonth(date) {
  let start = moment().startOf('month').format('YYYY-MM-DD') + '00:00:00';
  let end = moment().endOf('month').format('YYYY-MM-DD') + '23:59:59';

  return date >= start && date <= end;
}
